import * as React from "react";
import { Button } from "@material-ui/core";
import HeaderSlider from "./components/HeaderSlider";
import Jumbotron from "./components/Jumbotron";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";

import Container from "@material-ui/core/Container";
import "./index.css";
import profileImg from "./../images/Avatar-1.jpg";
import Skill from "./components/Skill"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

// markup
const IndexPage = () => {
  const classes = useStyles();
  return (
    <>
      <Jumbotron type="splat-toggle back-row-toggle" />

      <Container style={{ marginTop: -100 }}>
        <Grid container spacing={3}>
          <Grid item xs={4}>
            <img
              src={profileImg}
              style={{
                width: "80%",
                maxWidth: 400,
                margin: "auto",
                zIndex: 999,
              }}
              alt="Oliver Tembo"
            />
          </Grid>
          <Grid item xs={8}>
            <AppBar position="static">
              <Toolbar>
                <Button color="inherit">Projects</Button>
                <Button color="inherit">Services</Button>
                <Button color="inherit">Skills</Button>
                <Button color="inherit">About</Button>
              </Toolbar>
            </AppBar>

            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              <Typography variant="1" style={{ textAlign: "center" }}>
                Oliver Tembo
              </Typography>

              <Typography variant="3" style={{ textAlign: "center" }}>
                Full Stack Engineer
              </Typography>
              <ul>
                <li>
                  
                </li>
              </ul>
            </Paper>
          </Grid>
        </Grid>
      </Container>

      <Container style={{background:'linear-gradient(120deg, rgb(24, 24, 28), rgb(63, 81, 181))'}}>
        <Grid container spacing={3}>
          <Grid item xs={4}>
              <Skill />
          </Grid>
          <Grid item xs={4}>
            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              Paper
            </Paper>
          </Grid>
          <Grid item xs={4}>
            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              Paper
            </Paper>
          </Grid>
          <Grid item xs={4}>
            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              Paper
            </Paper>
          </Grid>
          <Grid item xs={4}>
            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              Paper
            </Paper>
          </Grid>
          <Grid item xs={4}>
            <Paper className={classes.paper} style={{ marginTop: "20px" }}>
              Paper
            </Paper>
          </Grid>
        </Grid>
      </Container>
    </>
  );
};

export default IndexPage;
